import React, { Component } from "react";
import styles from "./Controls.module.css";

class Controls extends Component {
  state = {
    type: "",
    amount: 0,
    balance: 0
  };

  handleClick = ({ target }) => {
    let inputValue = Number(document.querySelector("input").value);

    this.setState({
      type: target.value,
      amount: inputValue,
      encrease: 0,
      decrease: 0
    });

    target.value === "Deposit"
      ? this.setState(state => ({
          balance: state.balance + inputValue,
          encrease: inputValue
        }))
      : this.setState(state => ({
          balance: state.balance - inputValue,
          decrease: inputValue
        }));
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.onAddTransaction({ ...this.state });
    this.setState({ id: "", type: "", amount: "", date: "" });
  };

  render() {
    return (
      <form className={styles.controls} onSubmit={this.handleSubmit}>
        <div className={styles.controlsInner}>
          <input className={styles.input} type="text" name="text" />
          <button
            type="submit"
            value="Deposit"
            className={styles.button}
            onClick={this.handleClick}
          >
            Deposit
          </button>
          <button
            type="submit"
            value="Withdraw"
            className={styles.button}
            onClick={this.handleClick}
          >
            Withdraw
          </button>
        </div>
      </form>
    );
  }
}

export default Controls;
