import React, { Component } from 'react';
import Controls from '../Controls/Controls';
import Balance from '../Balance/Balance';
import TransactionHistory from '../Transaction/TransactionHistory';
import styles from '../Dashboard/Dashboard.module.css';
import shortid from 'shortid';

class Dashboard extends Component {
  state = {
    transactions: [],
    balance: 0,
    encrease: 0,
    decrease: 0,
  };

  localTransactions = JSON.parse(localStorage.getItem('transactions'));

  componentDidMount() {
    if (this.localTransactions) {
      this.setState(state => ({
        transactions: [...state.transactions, ...this.localTransactions],
      }));
    } else {
      this.setState(state => ({
        transactions: [...state.transactions],
      }));
    }
  }

  addTransaction = transaction => {
    const transToAdd = {
      ...transaction,
      id: shortid.generate(),
      date: new Date().toLocaleTimeString(),
    };

    this.setState(state => ({
      balance: transaction.balance,
      encrease: transaction.encrease,
      decrease: transaction.decrease,
      transactions: [...state.transactions, transToAdd],
    }));
  };

  render() {
    const { transactions, balance, encrease, decrease } = this.state;
    console.log(this.state.transactions);

    return (
      <div className={styles.dashboard}>
        <Controls onAddTransaction={this.addTransaction} />
        <Balance balance={balance} encrease={encrease} decrease={decrease} />
        <TransactionHistory transactions={transactions} />
      </div>
    );
  }
}

export default Dashboard;
